import React, { useEffect, useState } from 'react';
import { useParams, Link } from 'react-router-dom';
import { supabase } from '../supabaseClient';
import { ChevronLeft, ChevronUp, ChevronDown, Ruler, FileText, Check, Zap, Battery, Scale, Disc, Circle, MoveHorizontal, Maximize, GitBranch, Settings2, Layers, Link as LinkIcon, Plug, Monitor, Component, Hexagon, Activity, ArrowUpCircle } from 'lucide-react';

const ProductDetail = () => {
    const { id } = useParams();
    const [product, setProduct] = useState(null);
    const [loading, setLoading] = useState(true);
    const [selectedSize, setSelectedSize] = useState('M');
    const [relatedProducts, setRelatedProducts] = useState([]);

    const [activeImage, setActiveImage] = useState(null);

    useEffect(() => {
        fetchProduct();
    }, [id]);

    useEffect(() => {
        if (product) {
            if (product.images && product.images.length > 0) {
                setActiveImage(product.images[0]);
            } else {
                setActiveImage(product.image_url);
            }
        }
    }, [product]);

    const fetchProduct = async () => {
        try {
            setLoading(true);

            // Try 'bicicletas' first
            let { data, error } = await supabase
                .from('bicicletas')
                .select('*')
                .eq('id', id)
                .single();

            let mappedProduct = null;

            if (data) {
                mappedProduct = {
                    ...data,
                    name: data.modelo,
                    price: data.precio_eur,
                    description: data.description,
                    images: data.imagenes_urls || [],
                    image_url: data.imagenes_urls && data.imagenes_urls.length > 0 ? data.imagenes_urls[0] : null
                };
            } else {
                // Try 'products' table
                const { data: prodData, error: prodError } = await supabase
                    .from('products')
                    .select('*')
                    .eq('id', id)
                    .single();

                if (prodData) {
                    mappedProduct = {
                        ...prodData,
                        name: prodData.name,
                        price: prodData.price,
                        description: prodData.description,
                        images: prodData.image_url ? [prodData.image_url] : [],
                        image_url: prodData.image_url
                    };
                } else if (prodError && !error) {
                    // If both failed, use the error from first or second? 
                    // We'll throw generic or log.
                    console.error('Error fetching from products:', prodError);
                }
            }

            if (!mappedProduct) {
                // Nothing found
                setProduct(null);
            } else {
                setProduct(mappedProduct);

                // Fetch related products (same series)
                if (mappedProduct.serie_id) {
                    const { data: related } = await supabase
                        .from('bicicletas')
                        .select('id, modelo, precio_eur, imagenes_urls, imagen_url')
                        .eq('serie_id', mappedProduct.serie_id)
                        .neq('id', id) // Exclude current product
                        .limit(3);

                    if (related) {
                        setRelatedProducts(related);
                    }
                }
            }

        } catch (error) {
            console.error('Error fetching product:', error);
        } finally {
            setLoading(false);
        }
    };

    if (loading) return <div className="min-h-screen bg-white flex items-center justify-center"><div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-black"></div></div>;
    if (!product) return <div className="min-h-screen bg-white flex items-center justify-center">Producto no encontrado</div>;

    // Split name for styling "ARID PRO" -> "ARID" bold, "PRO" gray if possible, 
    // but for generic products we'll just display the name.
    // For the demo feeling, let's assume the last word might be the "variant" if it's multi-word.
    const mainName = product.name;
    const subName = '';

    return (
        <div className="bg-white min-h-screen pt-24 pb-16">
            <div className="max-w-[1600px] mx-auto px-4 sm:px-8 lg:px-12">

                {/* Breadcrumb / Back */}
                <div className="mb-8">
                    <Link to="/accesorios" className="inline-flex items-center text-sm font-bold uppercase tracking-wider text-black hover:text-gray-600 transition-colors">
                        <ChevronLeft className="w-4 h-4 mr-2" />
                        Back to Collection
                    </Link>
                </div>

                <div className="grid grid-cols-1 lg:grid-cols-12 gap-12 lg:gap-24">

                    {/* Left Column: Image Gallery */}
                    <div className="lg:col-span-7 relative">
                        <div className="flex bg-white rounded-3xl p-8 lg:p-12 shadow-sm border border-gray-100 h-[600px]">
                            {/* Thumbnails */}
                            <div className="hidden md:flex flex-col space-y-4 mr-8 overflow-y-auto max-h-full scrollbar-hide py-4">
                                {product.images && product.images.map((img, idx) => (
                                    <button
                                        key={idx}
                                        onClick={() => setActiveImage(img)}
                                        className={`w-16 h-16 rounded-xl border-2 overflow-hidden flex-shrink-0 transition-all ${activeImage === img ? 'border-black opacity-100' : 'border-gray-100 opacity-60 hover:opacity-100 hover:border-gray-300'}`}
                                    >
                                        <img src={img} alt={`Thumbnail ${idx}`} className="w-full h-full object-contain p-1" loading="lazy" />
                                    </button>
                                ))}
                            </div>

                            {/* Main Image */}
                            <div className="flex-1 flex items-center justify-center relative">
                                <img
                                    src={activeImage || product.image_url}
                                    alt={product.name}
                                    className="max-w-full max-h-full object-contain mix-blend-multiply transition-opacity duration-300"
                                    loading="eager"
                                    fetchPriority="high"
                                />
                            </div>
                        </div>
                    </div>

                    {/* Right Column: Details */}
                    <div className="lg:col-span-5 pt-4">
                        {/* Category / Breadcrumbs small */}
                        <div className="text-xs text-gray-400 mb-4 uppercase tracking-widest font-medium">
                            Accesorios / {product.category || 'General'}
                        </div>

                        {/* Title */}
                        <h1 className="text-5xl font-black uppercase tracking-tighter mb-8 text-black leading-none">
                            {mainName} <span className="text-gray-300">{subName}</span>
                        </h1>

                        {/* Description */}
                        <p className="text-gray-600 text-lg leading-relaxed mb-8">
                            {product.description || "Diseado para superar lmites. Este accesorio combina funcionalidad premium con el estilo inconfundible de Raymon. Rendimiento sin compromisos."}
                        </p>

                        {/* Tech Specs Mock */}
                        <div className="flex items-center space-x-6 mb-12 text-sm text-gray-500 font-medium">
                            <div className="flex items-center"><span className="w-6 h-6 rounded-full border border-gray-300 flex items-center justify-center mr-2 text-xs">R</span> Original</div>
                            <div className="flex items-center"><Check className="w-4 h-4 mr-2" /> Stock disponible</div>
                        </div>

                        {/* Color Selection Mock */}
                        <div className="mb-8">
                            <h3 className="text-sm font-bold uppercase tracking-wider mb-4">1. Choose Color</h3>
                            <div className="flex space-x-4">
                                <button className="w-8 h-8 rounded-full bg-red-600 ring-2 ring-offset-2 ring-black"></button>
                                {/* Mock hidden colors */}
                            </div>
                        </div>

                        {/* Size Selection Mock */}
                        <div className="mb-12">
                            <h3 className="text-sm font-bold uppercase tracking-wider mb-4">2. Choose Size(s)</h3>
                            <div className="grid grid-cols-4 gap-4">
                                {['S', 'M', 'L', 'XL'].map(size => (
                                    <button
                                        key={size}
                                        onClick={() => setSelectedSize(size)}
                                        className={`h-12 border flex items-center justify-center text-sm font-bold transition-all ${selectedSize === size
                                            ? 'border-2 border-black bg-white text-black'
                                            : 'border-gray-200 text-gray-500 hover:border-black'
                                            }`}
                                    >
                                        {size}
                                    </button>
                                ))}
                            </div>
                        </div>

                        {/* Action Links */}
                        <div className="flex space-x-8 mb-10 border-t border-b border-gray-100 py-6">
                            <button className="flex items-center text-xs font-bold uppercase tracking-widest hover:text-gray-600">
                                <Ruler className="w-4 h-4 mr-2" /> Size Guide
                            </button>
                            <button className="flex items-center text-xs font-bold uppercase tracking-widest hover:text-gray-600">
                                <FileText className="w-4 h-4 mr-2" /> Geometry
                            </button>
                        </div>

                        {/* Price & Add to Cart */}
                        <div className="flex items-center justify-between">
                            <div className="text-3xl font-black tracking-tight">{product.price} </div>
                            <button className="bg-black text-white px-12 py-4 text-sm font-bold uppercase tracking-widest hover:bg-gray-900 transition-transform transform hover:-translate-y-1">
                                Add to Cart
                            </button>
                        </div>

                    </div>
                </div>

                {/* Technical Specifications Section */}
                <div className="mt-32 border-t border-gray-100 pt-20">
                    <h2 className="text-3xl md:text-4xl font-black uppercase tracking-tighter text-center mb-20 text-black">
                        Especificaciones Tcnicas
                    </h2>

                    <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-x-12 gap-y-12 max-w-6xl mx-auto">
                        {specsMapping.map((spec, index) => {
                            const value = product[spec.key];
                            if (!value) return null;

                            return (
                                <div key={index} className="flex flex-col border-b border-gray-100 pb-4">
                                    <span className="text-xs font-bold uppercase text-gray-400 tracking-widest mb-2 flex items-center gap-3">
                                        {spec.Icon && <spec.Icon className="w-5 h-5 opacity-60" />}
                                        {spec.label}
                                    </span>
                                    <span className="font-bold text-lg text-black break-words">
                                        {spec.format ? spec.format(value) : value}
                                    </span>
                                </div>
                            );
                        })}
                    </div>
                </div>

                {/* Related Products Section */}
                {relatedProducts.length > 0 && (
                    <div className="mt-32 border-t border-gray-100 pt-20">
                        <h3 className="text-3xl md:text-4xl font-black uppercase tracking-tighter text-center mb-16 text-black">
                            Tambin te podra interesar
                        </h3>
                        <div className="grid grid-cols-1 md:grid-cols-3 gap-8 max-w-6xl mx-auto">
                            {relatedProducts.map(p => (
                                <Link to={`/product/${p.id}`} key={p.id} className="group block text-center" onClick={() => window.scrollTo(0, 0)}>
                                    <div className="bg-gray-50 rounded-3xl p-8 mb-6 relative aspect-[4/3] flex items-center justify-center transition-colors group-hover:bg-gray-100">
                                        <img
                                            src={p.imagenes_urls && p.imagenes_urls.length > 0 ? p.imagenes_urls[0] : p.imagen_url}
                                            alt={p.modelo}
                                            className="max-w-full max-h-full object-contain mix-blend-multiply group-hover:scale-105 transition-transform duration-500"
                                        />
                                    </div>
                                    <h4 className="text-xl font-black uppercase mb-2">{p.modelo}</h4>
                                    <p className="text-gray-500 font-medium text-lg">
                                        {new Intl.NumberFormat('es-ES', { style: 'currency', currency: 'EUR' }).format(p.precio_eur)}
                                    </p>
                                </Link>
                            ))}
                        </div>
                    </div>
                )}

            </div>
        </div>
    );
};

// Specs Definition
const specsMapping = [
    { label: 'Cuadro', key: 'cuadro_material', Icon: Maximize },
    { label: 'Horquilla', key: 'horquilla', Icon: GitBranch },
    { label: 'Amortiguador', key: 'shock', Icon: Activity },
    { label: 'Motor', key: 'motor_modelo', Icon: Zap },
    { label: 'Batera', key: 'bateria_wh', format: (val) => `${val} Wh`, Icon: Battery },
    { label: 'Cargador', key: 'charger', Icon: Plug },
    { label: 'Display', key: 'display', Icon: Monitor },
    { label: 'Frenos', key: 'frenos_modelo', Icon: Disc },
    { label: 'Discos de Freno', key: 'brake_discs', Icon: Disc },
    { label: 'Cambio', key: 'transmision_modelo', Icon: Settings2 },
    { label: 'Maneta de Cambio', key: 'shifter', Icon: Settings2 },
    { label: 'Cassette', key: 'cassette', Icon: Layers },
    { label: 'Cadena', key: 'chain', Icon: LinkIcon },
    { label: 'Bielas', key: 'crank_arm', Icon: Component },
    { label: 'Plato', key: 'chainring', Icon: Circle },
    { label: 'Ruedas', key: 'wheelset', Icon: Circle },
    { label: 'Neumtico Delantero', key: 'tire_f', Icon: Circle },
    { label: 'Neumtico Trasero', key: 'tire_r', Icon: Circle },
    { label: 'Manillar', key: 'handlebar', Icon: MoveHorizontal },
    { label: 'Potencia', key: 'stem', Icon: Component },
    { label: 'Silln', key: 'saddle', Icon: ArrowUpCircle },
    { label: 'Tija', key: 'tija_telescopica', Icon: ArrowUpCircle },
    { label: 'Pedales', key: 'pedals', Icon: Hexagon },
    { label: 'Peso', key: 'peso_kg', format: (val) => `${val} kg`, Icon: Scale },
];

export default ProductDetail;
