import React from 'react';
import { Facebook, Instagram, Youtube } from 'lucide-react';

const Footer = () => {
    return (
        <footer className="bg-black text-white py-16">
            <div className="max-w-[1920px] mx-auto px-4 sm:px-6 lg:px-8">
                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-12 text-sm">

                    {/* Column 1: Brand */}
                    <div className="space-y-6">
                        <h2 className="text-3xl font-black uppercase tracking-tighter">RAYMON</h2>
                        <p className="text-gray-400">
                            Bicicletas diseadas para cada camino.<br />
                            Ingeniera alemana. Pasin global.
                        </p>
                        <div className="flex space-x-4">
                            <a href="#" className="hover:text-gray-400 transition-colors"><Instagram className="w-5 h-5" /></a>
                            <a href="#" className="hover:text-gray-400 transition-colors"><Facebook className="w-5 h-5" /></a>
                            <a href="#" className="hover:text-gray-400 transition-colors"><Youtube className="w-5 h-5" /></a>
                        </div>
                    </div>

                    {/* Column 2: E-Bikes */}
                    <div>
                        <h3 className="font-bold uppercase tracking-wider mb-4">E-Bikes</h3>
                        <ul className="space-y-3 text-gray-400">
                            <li><a href="#" className="hover:text-white transition-colors">E-MTB</a></li>
                            <li><a href="#" className="hover:text-white transition-colors">E-Trekking</a></li>
                            <li><a href="#" className="hover:text-white transition-colors">E-City</a></li>
                            <li><a href="#" className="hover:text-white transition-colors">E-Kids</a></li>
                        </ul>
                    </div>

                    {/* Column 3: Bikes */}
                    <div>
                        <h3 className="font-bold uppercase tracking-wider mb-4">Bicicletas</h3>
                        <ul className="space-y-3 text-gray-400">
                            <li><a href="#" className="hover:text-white transition-colors">Mountain Bike (MTB)</a></li>
                            <li><a href="#" className="hover:text-white transition-colors">Gravel</a></li>
                            <li><a href="#" className="hover:text-white transition-colors">Trekking</a></li>
                            <li><a href="#" className="hover:text-white transition-colors">City</a></li>
                            <li><a href="#" className="hover:text-white transition-colors">Nios</a></li>
                        </ul>
                    </div>

                    {/* Column 4: Service & Legal */}
                    <div>
                        <h3 className="font-bold uppercase tracking-wider mb-4">Servicio</h3>
                        <ul className="space-y-3 text-gray-400">
                            <li><a href="#" className="hover:text-white transition-colors">Concesionarios</a></li>
                            <li><a href="#" className="hover:text-white transition-colors">Manuales</a></li>
                            <li><a href="#" className="hover:text-white transition-colors">Garanta</a></li>
                            <li><a href="#" className="hover:text-white transition-colors">Contacto</a></li>
                        </ul>
                    </div>

                </div>

                <div className="mt-16 pt-8 border-t border-gray-800 text-xs text-gray-500 flex flex-col md:flex-row justify-between items-center">
                    <p>&copy; 2024 Raymon Bicycles. Todos los derechos reservados.</p>
                    <div className="flex space-x-6 mt-4 md:mt-0">
                        <a href="#" className="hover:text-white">Privacidad</a>
                        <a href="#" className="hover:text-white">Imprint</a>
                        <a href="#" className="hover:text-white">Cookies</a>
                    </div>
                </div>
            </div>
        </footer>
    );
};

export default Footer;
