import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import { ChevronRight } from 'lucide-react';

const categories = [
    {
        id: 'accessories', label: 'Accesorios', products: []
    }
];

const MegaMenu = ({ onMouseLeave }) => {
    const [activeCategory, setActiveCategory] = useState('accessories');
    const bgImage = "https://images.unsplash.com/photo-1532298229144-0ec0c57515c7?auto=format&fit=crop&q=80&w=800"; // Generic bike image for now

    const currentCategory = categories.find(c => c.id === activeCategory) || categories[0];

    return (
        <div
            className="absolute top-full left-0 w-full bg-white shadow-xl border-t border-gray-100 transform transition-all duration-300 ease-in-out z-40"
            onMouseLeave={onMouseLeave}
        >
            <div className="max-w-[1920px] mx-auto px-4 sm:px-6 lg:px-8">
                <div className="grid grid-cols-12 h-[500px]">

                    {/* Sidebar / Categories */}
                    <div className="col-span-2 bg-gray-50 py-8 border-r border-gray-100">
                        <div className="flex flex-col space-y-1">
                            {categories.map((cat) => (
                                <button
                                    key={cat.id}
                                    onMouseEnter={() => setActiveCategory(cat.id)}
                                    className={`text-left px-8 py-3 text-sm font-bold uppercase tracking-wider flex items-center justify-between group transition-colors ${activeCategory === cat.id
                                        ? 'bg-white text-black border-l-4 border-black'
                                        : 'text-gray-500 hover:text-black hover:bg-gray-100'
                                        }`}
                                >
                                    <span>{cat.label}</span>
                                    {activeCategory === cat.id && <ChevronRight className="w-4 h-4" />}
                                </button>
                            ))}
                        </div>
                        <div className="mt-8 px-8">
                            <Link to="/accesorios" className="text-xs font-bold uppercase tracking-widest text-black flex items-center hover:underline">
                                Ver todo Accesorios <ChevronRight className="w-3 h-3 ml-1" />
                            </Link>
                        </div>
                    </div>

                    {/* Content Grid */}
                    <div className="col-span-7 p-12 overflow-y-auto">
                        <div className="mb-6">
                            <h3 className="text-2xl font-black uppercase tracking-tight mb-2">{currentCategory.label}</h3>
                            <p className="text-gray-500 text-sm">Explora nuestra coleccin de {currentCategory.label.toLowerCase()}.</p>
                        </div>

                        <div className="grid grid-cols-3 gap-8">
                            <div className="col-span-3 text-center py-12">
                                <Link to="/accesorios" className="inline-block px-8 py-4 bg-black text-white font-bold uppercase tracking-widest hover:bg-gray-900 transition-colors">
                                    Ver todos los Accesorios
                                </Link>
                            </div>
                        </div>
                    </div>

                    {/* Promo Area */}
                    <div className="col-span-3 relative overflow-hidden group cursor-pointer">
                        <img
                            src={bgImage}
                            alt="Promo"
                            className="absolute inset-0 w-full h-full object-cover transition-transform duration-700 group-hover:scale-105"
                        />
                        <div className="absolute inset-0 bg-black/30 flex flex-col justify-end p-12 text-white">
                            <h4 className="text-2xl font-black uppercase tracking-tight mb-2">Raymon Care</h4>
                            <p className="text-sm font-medium mb-4 opacity-90">Safety that rides with you. Register your bike now.</p>
                            <span className="text-xs font-bold uppercase tracking-widest flex items-center">
                                Register now <ChevronRight className="w-4 h-4 ml-1" />
                            </span>
                        </div>
                    </div>

                </div>
            </div>
        </div>
    );
};

export default MegaMenu;
