import React, { useEffect, useState } from 'react';
import { useParams, Link } from 'react-router-dom';
import { supabase } from '../supabaseClient';
import { ChevronRight, ChevronLeft, ArrowRight } from 'lucide-react';

const SeriesDetail = () => {
    const { serieId } = useParams();
    const [bikes, setBikes] = useState([]);
    const [loading, setLoading] = useState(true);
    const [seriesName, setSeriesName] = useState('');
    const [activeBikeIndex, setActiveBikeIndex] = useState(0);
    const [direction, setDirection] = useState('next'); // 'next' or 'prev'

    const handlePrev = () => {
        setDirection('prev');
        setActiveBikeIndex(prev => (prev === 0 ? bikes.length - 1 : prev - 1));
    };

    const handleNext = () => {
        setDirection('next');
        setActiveBikeIndex(prev => (prev === bikes.length - 1 ? 0 : prev + 1));
    };

    useEffect(() => {
        const fetchSeries = async () => {
            try {
                setLoading(true);
                const { data, error } = await supabase
                    .from('bicicletas')
                    .select('*')
                    .eq('serie_id', serieId);

                if (error) throw error;

                if (data && data.length > 0) {
                    setBikes(data);
                    // Infer series name from the first model (e.g., "Ravor Pro" -> "RAVOR")
                    const firstModel = data[0].modelo;
                    setSeriesName(firstModel.split(' ')[0].toUpperCase());
                }
            } catch (err) {
                console.error('Error fetching series:', err);
            } finally {
                setLoading(false);
            }
        };

        if (serieId) {
            fetchSeries();
        }
    }, [serieId]);

    if (loading) {
        return (
            <div className="min-h-screen flex items-center justify-center bg-white">
                <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-black"></div>
            </div>
        );
    }

    if (bikes.length === 0) {
        return <div className="min-h-screen flex items-center justify-center">Series not found</div>;
    }

    // Use carousel state for hero representation
    // const heroBike = bikes[activeBikeIndex]; // Implicit usage inside JSX

    return (
        <div className="bg-white min-h-screen ">
            {/* Header / Hero Section */}
            <div
                className="relative pb-20 overflow-hidden"
                style={{
                    backgroundImage: "url('https://www.raymon-bicycles.com/_next/image?url=https%3A%2F%2Fb2b.raymon-bicycles.com%2Fmedia%2F33%2F03%2F3d%2F1749728337%2FRavor_MainHeader.png&w=640&q=100')",
                    backgroundSize: 'cover',
                    backgroundPosition: 'center',
                    backgroundRepeat: 'no-repeat'
                }}
            >
                <div className="max-w-[1600px] mx-auto px-8 pt-12 text-center">
                    {/* Breadcrumbs */}
                    <div className="text-xs font-bold uppercase tracking-widest text-gray-400 mb-8 flex justify-center gap-2">
                        <span>Bikes</span> / <span>Fully</span> / <span>Modelle</span> / <span className="text-black">{seriesName}</span>
                    </div>

                    <h1 className="text-6xl md:text-9xl font-black uppercase tracking-tighter text-black mb-12">
                        {seriesName}
                    </h1>

                    {/* Hero Carousel */}
                    <div className="relative w-full mt-32 mb-20">
                        <div
                            className="flex items-end justify-center h-[500px] relative perspective-[1000px] overflow-visible"
                        >

                            {/* Previous Button */}
                            <button
                                onClick={handlePrev}
                                className="absolute left-4 md:left-16 top-1/2 -translate-y-1/2 z-40 w-12 h-12 rounded-full border border-gray-200 bg-white/80 flex items-center justify-center text-gray-400 hover:text-black hover:border-black transition-all backdrop-blur-sm shadow-sm"
                            >
                                <ChevronLeft className="w-6 h-6" />
                            </button>

                            {/* Carousel Items */}
                            {bikes.map((bike, index) => {
                                // Determine position state
                                let position = 'hidden';
                                const len = bikes.length;
                                if (index === activeBikeIndex) position = 'active';
                                else if (index === (activeBikeIndex - 1 + len) % len) position = 'prev';
                                else if (index === (activeBikeIndex + 1) % len) position = 'next';

                                // Base styles
                                let baseClasses = "absolute top-0 w-full md:w-[60%] h-full flex flex-col justify-end items-center transition-all duration-700 ease-in-out";
                                let stateClasses = "";

                                switch (position) {
                                    case 'active':
                                        stateClasses = "left-1/2 -translate-x-1/2 scale-100 opacity-100 z-30 pointer-events-auto";
                                        break;
                                    case 'prev':
                                        stateClasses = "left-1/2 -translate-x-[120%] md:left-[5%] md:-translate-x-1/2 scale-50 opacity-40 z-20 grayscale pointer-events-none";
                                        break;
                                    case 'next':
                                        stateClasses = "left-1/2 translate-x-[20%] md:left-[95%] md:-translate-x-1/2 scale-50 opacity-40 z-20 grayscale pointer-events-none";
                                        break;
                                    default:
                                        stateClasses = "left-1/2 -translate-x-1/2 scale-50 opacity-0 z-10 pointer-events-none";
                                }

                                return (
                                    <div key={bike.id} className={`${baseClasses} ${stateClasses}`}>
                                        <Link to={`/product/${bike.id}`} className="block w-full h-full flex flex-col justify-end items-center group">
                                            <div className="flex-1 w-full flex items-center justify-center p-8">
                                                <img
                                                    src={bike.imagenes_urls ? bike.imagenes_urls[0] : bike.imagen_url}
                                                    alt={bike.modelo}
                                                    className="max-w-full max-h-full object-contain mix-blend-multiply drop-shadow-xl"
                                                />
                                            </div>
                                            <div className={`text-center mt-4 transition-opacity duration-500 ${position === 'active' ? 'opacity-100' : 'opacity-0'}`}>
                                                <h2 className="text-3xl font-black uppercase tracking-tight mb-2">
                                                    {bike.modelo}
                                                </h2>
                                                <p className="text-xl text-gray-500 font-medium">
                                                    {new Intl.NumberFormat('es-ES', { style: 'currency', currency: 'EUR' }).format(bike.precio_eur)}
                                                </p>
                                            </div>
                                        </Link>
                                    </div>
                                );
                            })}

                            {/* Next Button */}
                            <button
                                onClick={handleNext}
                                className="absolute right-4 md:right-16 top-1/2 -translate-y-1/2 z-40 w-12 h-12 rounded-full border border-gray-200 bg-white/80 flex items-center justify-center text-gray-400 hover:text-black hover:border-black transition-all backdrop-blur-sm shadow-sm"
                            >
                                <ChevronRight className="w-6 h-6" />
                            </button>
                        </div>

                        {/* Pagination Dots */}
                        <div className="flex justify-center gap-3 mt-8">
                            {bikes.map((_, idx) => (
                                <button
                                    key={idx}
                                    onClick={() => setActiveBikeIndex(idx)}
                                    className={`w-2 h-2 rounded-full transition-all ${idx === activeBikeIndex ? 'bg-black w-4' : 'bg-gray-300 hover:bg-gray-400'}`}
                                />
                            ))}
                        </div>
                    </div>

                    {/* Specs / Info Bar */}
                    <div className="mt-16 bg-white inline-flex flex-wrap items-center justify-center rounded-tl-[3rem] rounded-br-[3rem] shadow-lg border border-gray-100 p-6 w-full max-w-[1100px] mx-auto">
                        <div className="flex-1 min-w-[140px] text-left px-6 py-4 border-r border-gray-100 last:border-0">
                            <span className="block text-xs font-bold uppercase text-gray-400 mb-2">Frame</span>
                            <span className="font-black text-xl uppercase">{bikes[activeBikeIndex]?.cuadro_material || 'N/A'}</span>
                        </div>
                        <div className="flex-1 min-w-[140px] text-left px-6 py-4 border-r border-gray-100 last:border-0">
                            <span className="block text-xs font-bold uppercase text-gray-400 mb-2">Fork</span>
                            <span className="font-black text-xl uppercase">{bikes[activeBikeIndex]?.horquilla || 'N/A'}</span>
                        </div>
                        <div className="flex-1 min-w-[140px] text-left px-6 py-4 border-r border-gray-100 last:border-0">
                            <span className="block text-xs font-bold uppercase text-gray-400 mb-2">Weight</span>
                            <span className="font-black text-xl uppercase">{bikes[activeBikeIndex]?.peso_kg ? `${bikes[activeBikeIndex]?.peso_kg} kg` : 'N/A'}</span>
                        </div>
                        <div className="flex-1 min-w-[140px] text-left px-6 py-4 border-r border-gray-100 last:border-0">
                            <span className="block text-xs font-bold uppercase text-gray-400 mb-2">Motor</span>
                            <span className="font-black text-xl uppercase">{bikes[activeBikeIndex]?.motor_modelo || 'N/A'}</span>
                        </div>
                        <div className="flex-1 min-w-[140px] text-left px-6 py-4">
                            <span className="block text-xs font-bold uppercase text-gray-400 mb-2">Battery</span>
                            <span className="font-black text-xl uppercase">{bikes[activeBikeIndex]?.bateria_wh ? `${bikes[activeBikeIndex]?.bateria_wh} Wh` : 'N/A'}</span>
                        </div>
                    </div>
                </div>
            </div>

            {/* Marketing Text */}
            <div className="py-24 text-center px-4">
                <h2 className="text-4xl md:text-5xl font-bold text-gray-300 uppercase mb-4">
                    Ride me once.
                </h2>
                <h2 className="text-4xl md:text-5xl font-black text-black uppercase mb-12">
                    Crave me forever.
                </h2>
                <div className="max-w-3xl mx-auto text-gray-600 space-y-6 leading-relaxed">
                    <p>
                        You are in a frenzy. Dust swirls up, you drift slightly and stay on it. The edge comes - and pulls. Air. Calm. Pulse. Then you touch down. Full and clean. Just you and your {seriesName} and a big grin that sticks to your face. Because once you've felt it, you know: this is what life feels like.
                    </p>
                    <p className="text-sm font-bold">
                        The {seriesName} is your energy booster. Your "Yeah man, that's exactly why I ride a bike!"
                    </p>
                </div>

                <button className="mt-12 bg-black text-white px-10 py-4 font-bold uppercase tracking-widest text-sm hover:bg-gray-800 transition-colors">
                    Join the waitlist
                </button>
            </div>

            {/* Variants Grid */}
            <div className="bg-[#fafafa] py-24">
                <div className="max-w-[1600px] mx-auto px-8">
                    <h3 className="text-4xl font-black uppercase text-center mb-16">
                        Descubre todos los {seriesName}s.
                    </h3>

                    <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
                        {bikes.map(bike => (
                            <div key={bike.id} className="bg-white p-8 flex flex-col items-center text-center group">
                                <Link to={`/product/${bike.id}`} className="block w-full">
                                    <div className="aspect-[4/3] mb-8 relative">
                                        <img
                                            src={bike.imagenes_urls ? bike.imagenes_urls[0] : bike.imagen_url}
                                            alt={bike.modelo}
                                            className="w-full h-full object-contain mix-blend-multiply transition-transform duration-500 group-hover:scale-105"
                                        />
                                    </div>
                                    <h4 className="text-2xl font-black uppercase mb-2">{bike.modelo}</h4>
                                    <p className="text-gray-500 font-medium mb-8">
                                        {new Intl.NumberFormat('es-ES', { style: 'currency', currency: 'EUR' }).format(bike.precio_eur)}
                                    </p>
                                    <span className="inline-block border border-black px-8 py-3 text-xs font-bold uppercase tracking-widest hover:bg-black hover:text-white transition-colors">
                                        Show Bike
                                    </span>
                                </Link>
                            </div>
                        ))}
                    </div>
                </div>
            </div>
        </div>
    );
};

export default SeriesDetail;
