import React, { useState, useEffect } from 'react';
import { supabase } from '../../supabaseClient';
import { ArrowRight, ChevronLeft, ChevronRight } from 'lucide-react';
import { Link } from 'react-router-dom';
import eBikesImage from '../../assets/Raymon-2024-nauders-1715.webp';
import bikesImage from '../../assets/Raymon-2024-nauders-1874.webp';

const tabs = [
    { id: 'e-mtb', label: 'E-MTB' },
    { id: 'e-gravel', label: 'E-Gravel' },
    { id: 'e-trekking', label: 'E-Trekking' },
    { id: 'e-city', label: 'E-City' },
];

const JourneySection = () => {
    const [activeTab, setActiveTab] = useState('e-mtb');
    const [products, setProducts] = useState([]);
    const [loading, setLoading] = useState(false);
    const [activeProductIndex, setActiveProductIndex] = useState(0);

    const categoryMap = {
        'e-mtb': 1,
        'e-gravel': 2,
        'e-trekking': 3,
        'e-city': 4
    };

    // Reverse map for sync
    const idToCategoryMap = {
        1: 'e-mtb',
        2: 'e-gravel',
        3: 'e-trekking',
        4: 'e-city'
    };

    useEffect(() => {
        fetchProducts();
    }, []);

    // Sync Active Tab with Slide
    useEffect(() => {
        if (products.length > 0) {
            const currentSerieId = products[activeProductIndex]?.serie_id;
            const currentTab = idToCategoryMap[currentSerieId];
            if (currentTab && currentTab !== activeTab) {
                setActiveTab(currentTab);
            }
        }
    }, [activeProductIndex, products]);

    const handlePrev = () => {
        setActiveProductIndex(prev => (prev === 0 ? products.length - 1 : prev - 1));
    };

    const handleNext = () => {
        setActiveProductIndex(prev => (prev === products.length - 1 ? 0 : prev + 1));
    };

    const handleTabClick = (tabId) => {
        // Find index of first product with this category's serie_id
        const targetSerieId = categoryMap[tabId];
        const index = products.findIndex(p => p.serie_id === targetSerieId);
        if (index !== -1) {
            setActiveProductIndex(index);
            setActiveTab(tabId);
        }
    };

    const fetchProducts = async () => {
        setLoading(true);
        try {
            const { data, error } = await supabase
                .from('bicicletas')
                .select('*')
                .order('serie_id', { ascending: true }); // Ensure order matches tabs roughly

            if (error) throw error;

            // Filter: One per serie_id, prioritizing those with images
            const bestBySeries = {};

            (data || []).forEach(item => {
                const existing = bestBySeries[item.serie_id];
                const hasImg = item.imagenes_urls && item.imagenes_urls.length > 0;

                // If no entry for this series, or current item has image and existing doesn't, use current.
                if (!existing || (hasImg && (!existing.imagenes_urls || existing.imagenes_urls.length === 0))) {
                    bestBySeries[item.serie_id] = item;
                }
            });

            // Convert to array and ensure sorted by serie_id
            const uniqueSeries = Object.values(bestBySeries).sort((a, b) => a.serie_id - b.serie_id);

            // Map DB fields to Component fields
            const mappedData = uniqueSeries.map(item => ({
                id: item.id,
                name: item.modelo,
                price: item.precio_eur,
                image_url: item.imagenes_urls && item.imagenes_urls.length > 0 ? item.imagenes_urls[0] : null,
                serie_id: item.serie_id,
                category: idToCategoryMap[item.serie_id] || 'e-mtb'
            }));

            setProducts(mappedData);
        } catch (error) {
            console.error('Error fetching journey products:', error);
            setProducts([]);
        } finally {
            setLoading(false);
        }
    };

    return (
        <section className="py-24 bg-white">
            <div className="max-w-[1600px] mx-auto px-8 sm:px-12 lg:px-16">

                {/* Header */}
                <div className="mb-16">
                    <h2 className="text-4xl md:text-5xl font-black uppercase tracking-tighter mb-4 text-black">
                        Cul es tu estilo?
                    </h2>
                    <p className="text-gray-500 max-w-3xl text-lg">
                        Descubre un nuevo mundo con cada bicicleta. Ya sea en rutas suaves o paisajes urbanos, cada bicicleta conquista tu camino contigo.
                    </p>
                </div>

                <div className="grid grid-cols-1 lg:grid-cols-12 gap-8">

                    {/* Left Column: Category Promos */}
                    <div className="lg:col-span-4 flex flex-col gap-8">
                        {/* E-Bikes Card */}
                        <div className="relative h-[300px] rounded-tl-[3rem] rounded-br-[3rem] overflow-hidden group cursor-pointer bg-gray-100">
                            <img src="https://rwbxersfwgmkixulhnxp.supabase.co/storage/v1/object/sign/bicicletas/Assetes%20web%20page/Raymon-2024-nauders-1715.jpg?token=eyJraWQiOiJzdG9yYWdlLXVybC1zaWduaW5nLWtleV9iZjU5MzQwZS1mMGM0LTRkM2QtYmNiZi1kZjRlY2MyMWNkNTIiLCJhbGciOiJIUzI1NiJ9.eyJ1cmwiOiJiaWNpY2xldGFzL0Fzc2V0ZXMgd2ViIHBhZ2UvUmF5bW9uLTIwMjQtbmF1ZGVycy0xNzE1LmpwZyIsImlhdCI6MTc2NzgxODIxNCwiZXhwIjo4ODE2NzczMTgxNH0.qXVLCoToq3ypqNG5ATndnfpsr--7kP4rwLSV8vSm5C8" alt="E-Bikes" className="absolute inset-0 w-full h-full object-cover transition-transform duration-700 group-hover:scale-110" />
                            <div className="absolute inset-0 bg-black/20 group-hover:bg-black/30 transition-colors"></div>
                            <div className="absolute bottom-8 left-8">
                                <h3 className="text-3xl font-black text-white uppercase italic tracking-tight">E-Bikes</h3>
                            </div>
                        </div>

                        {/* Bikes Card */}
                        <div className="relative h-[300px] rounded-tl-lg rounded-br-lg rounded-tr-[3rem] rounded-bl-[3rem] overflow-hidden group cursor-pointer bg-gray-100">
                            <img src="https://rwbxersfwgmkixulhnxp.supabase.co/storage/v1/object/sign/bicicletas/Assetes%20web%20page/Raymon-2024-nauders-1874.jpg?token=eyJraWQiOiJzdG9yYWdlLXVybC1zaWduaW5nLWtleV9iZjU5MzQwZS1mMGM0LTRkM2QtYmNiZi1kZjRlY2MyMWNkNTIiLCJhbGciOiJIUzI1NiJ9.eyJ1cmwiOiJiaWNpY2xldGFzL0Fzc2V0ZXMgd2ViIHBhZ2UvUmF5bW9uLTIwMjQtbmF1ZGVycy0xODc0LmpwZyIsImlhdCI6MTc2NzgxODQ3MywiZXhwIjo4ODE2NzczMjA3M30.Xux1DNkIGQuR-4kWxK9GzmurZ4-Pxs8BiPgLjqsOi_A" alt="Bikes" className="absolute inset-0 w-full h-full object-cover transition-transform duration-700 group-hover:scale-110" />
                            <div className="absolute inset-0 bg-black/20 group-hover:bg-black/30 transition-colors"></div>
                            <div className="absolute bottom-8 left-8">
                                <h3 className="text-3xl font-black text-white uppercase italic tracking-tight">Bicicletas</h3>
                            </div>
                        </div>
                    </div>

                    {/* Right Column: Tabs & Product Display */}
                    <div className="lg:col-span-8 bg-gray-50 rounded-2xl p-8 lg:p-12">

                        {/* Tabs */}
                        <div className="flex flex-wrap items-center justify-between mb-12 border-b border-gray-200 pb-4">
                            <div className="flex space-x-8 overflow-x-auto pb-2">
                                {tabs.map(tab => (
                                    <button
                                        key={tab.id}
                                        onClick={() => handleTabClick(tab.id)}
                                        className={`text-sm font-bold uppercase tracking-widest pb-4 border-b-2 transition-colors whitespace-nowrap ${activeTab === tab.id
                                            ? 'border-black text-black'
                                            : 'border-transparent text-gray-400 hover:text-gray-600'
                                            }`}
                                    >
                                        {tab.label}
                                    </button>
                                ))}
                            </div>
                            {/* Controls (Visual only for now) */}
                            <div className="hidden sm:flex space-x-2">
                                <button className="w-8 h-8 rounded-full border border-gray-300 flex items-center justify-center text-gray-400 hover:text-black hover:border-black transition-all">
                                    <ChevronLeft className="w-4 h-4" />
                                </button>
                                <button className="w-8 h-8 rounded-full border border-gray-300 flex items-center justify-center text-gray-400 hover:text-black hover:border-black transition-all">
                                    <ChevronRight className="w-4 h-4" />
                                </button>
                            </div>
                        </div>

                        <div className="relative min-h-[500px] flex flex-col justify-between">
                            {loading ? (
                                <div className="absolute inset-0 flex items-center justify-center bg-white/50 z-10">
                                    <div className="animate-spin rounded-full h-8 w-8 border-t-2 border-b-2 border-black"></div>
                                </div>
                            ) : (
                                <>
                                    <div className="flex-1 relative overflow-hidden">
                                        {products.length > 0 ? (
                                            <div className="flex items-end h-[450px] transition-all duration-500 ease-in-out">
                                                {/* Active Product - Larger Image, Smaller Text */}
                                                <div className="w-3/4 pr-8 flex-shrink-0 relative z-10">
                                                    <Link to={`/serie/${products[activeProductIndex]?.serie_id}`} className="block group h-full flex flex-col justify-end">
                                                        <div className="flex-1 flex items-center justify-center mb-6">
                                                            <img
                                                                src={products[activeProductIndex]?.image_url}
                                                                alt={products[activeProductIndex]?.name}
                                                                className="max-h-full max-w-full object-contain mix-blend-multiply transition-transform duration-500 group-hover:scale-105"
                                                                loading="eager"
                                                                fetchPriority="high"
                                                            />
                                                        </div>
                                                        <div className="mt-2 pl-2">
                                                            <div className="flex items-center gap-2 mb-1">
                                                                <h3 className="text-xl font-bold text-black tracking-tight uppercase">
                                                                    {products[activeProductIndex]?.name}
                                                                </h3>
                                                                <svg className="w-4 h-4 text-gray-300" viewBox="0 0 24 24" fill="currentColor"><path d="M13 2L3 14H12L11 22L21 10H12L13 2Z" /></svg>
                                                            </div>
                                                            <p className="text-sm text-gray-500 font-medium">
                                                                {new Intl.NumberFormat('es-ES', { style: 'currency', currency: 'EUR' }).format(products[activeProductIndex]?.price)}
                                                            </p>
                                                        </div>
                                                    </Link>
                                                </div>

                                                {/* Next Product (Preview) */}
                                                {products.length > 1 && (
                                                    <div className="w-1/2 flex-shrink-0 opacity-40 grayscale scale-75 origin-bottom-left translate-x-[-15%] select-none pointer-events-none pb-8">
                                                        <div className="aspect-[4/3] flex items-center justify-center mb-4">
                                                            <img
                                                                src={products[(activeProductIndex + 1) % products.length]?.image_url}
                                                                alt="Next"
                                                                className="max-h-full max-w-full object-contain mix-blend-multiply"
                                                                loading="lazy"
                                                            />
                                                        </div>
                                                        <div className="mt-2 pl-4">
                                                            <h3 className="text-lg font-bold text-gray-400 tracking-tight uppercase truncate">
                                                                {products[(activeProductIndex + 1) % products.length]?.name}
                                                            </h3>
                                                        </div>
                                                    </div>
                                                )}
                                            </div>
                                        ) : (
                                            <div className="h-full flex flex-col items-center justify-center text-gray-400">
                                                <p>No hay productos disponibles.</p>
                                            </div>
                                        )}
                                    </div>

                                    {/* Bottom Controls */}
                                    <div className="mt-12 flex items-center justify-between border-t border-gray-100 pt-8">
                                        {/* Left: View All Button */}
                                        <Link to="/accesorios" className="px-8 py-3 bg-gray-100 hover:bg-black hover:text-white transition-colors text-xs font-bold uppercase tracking-widest rounded-sm">
                                            Alle {activeTab.toUpperCase()}
                                        </Link>

                                        {/* Right: Navigation */}
                                        <div className="flex items-center gap-8">
                                            {/* Dots */}
                                            <div className="flex gap-3">
                                                {products.map((_, idx) => (
                                                    <button
                                                        key={idx}
                                                        onClick={() => setActiveProductIndex(idx)}
                                                        className={`w-2 h-2 rounded-full transition-all ${idx === activeProductIndex ? 'bg-black w-2' : 'bg-gray-300 hover:bg-gray-400'}`}
                                                    />
                                                ))}
                                            </div>

                                            {/* Arrows */}
                                            <div className="flex gap-2">
                                                <button
                                                    onClick={handlePrev}
                                                    className="w-10 h-10 rounded-full border border-gray-200 flex items-center justify-center text-gray-400 hover:text-black hover:border-black transition-all"
                                                >
                                                    <ChevronLeft className="w-5 h-5" />
                                                </button>
                                                <button
                                                    onClick={handleNext}
                                                    className="w-10 h-10 rounded-full border border-gray-200 flex items-center justify-center text-gray-400 hover:text-black hover:border-black transition-all"
                                                >
                                                    <ChevronRight className="w-5 h-5" />
                                                </button>
                                            </div>
                                        </div>
                                    </div>
                                </>
                            )}
                        </div>

                    </div>
                </div>
            </div>
        </section>
    );
};

export default JourneySection;
