import React, { useState } from 'react';
import { Search, MapPin, Globe, Menu, X, ShoppingBag } from 'lucide-react';
import { Link } from 'react-router-dom';
import MegaMenu from './MegaMenu';
import raymonLogo from '../../assets/Raymon_logo_black schriftzug.png';
import proomtbLogo from '../../assets/LOGO PRO MTB AND ROAD VECTORES CORREGIDOS.pdf.png';

const Header = () => {
    const [isMenuOpen, setIsMenuOpen] = useState(false);
    const [showMegaMenu, setShowMegaMenu] = useState(false);

    return (
        <header className="fixed w-full top-0 z-50 bg-white border-b border-gray-100">
            <div className="max-w-[1920px] mx-auto px-4 sm:px-6 lg:px-8">
                <div className="flex justify-between items-center h-20">

                    {/* Logo */}
                    <div className="flex-shrink-0 flex items-center space-x-4">
                        <Link to="/" className="flex items-center space-x-4">
                            <img src={proomtbLogo} alt="Proomtb Logo" className="h-12 w-auto object-contain" />
                            <div className="h-8 w-px bg-gray-300"></div>
                            <img src={raymonLogo} alt="Raymon Logo" className="h-8 w-auto object-contain" />
                        </Link>
                    </div>

                    {/* Desktop Navigation */}
                    <nav className="hidden lg:flex space-x-8 items-center">
                        <div className="group relative">
                            <button className="text-sm font-bold uppercase tracking-wider hover:text-gray-600 transition-colors">
                                E-Bikes
                            </button>
                        </div>
                        <div className="group relative">
                            <button className="text-sm font-bold uppercase tracking-wider hover:text-gray-600 transition-colors">
                                Bicicletas
                            </button>
                        </div>
                        <div
                            className="group"
                            onMouseEnter={() => setShowMegaMenu(true)}
                            onMouseLeave={() => setShowMegaMenu(false)}
                        >
                            <Link
                                to="/accesorios"
                                className="inline-flex items-center h-20 px-1 text-sm font-bold uppercase tracking-wider hover:text-gray-600 transition-colors relative z-50"
                            >
                                Accesorios
                            </Link>

                            {/* Mega Menu Container */}
                            <div className={`transition-opacity duration-200 ${showMegaMenu ? 'opacity-100 visible' : 'opacity-0 invisible'}`}>
                                {showMegaMenu && <MegaMenu onMouseLeave={() => setShowMegaMenu(false)} />}
                            </div>
                        </div>
                        <div className="group relative">
                            <button className="text-sm font-bold uppercase tracking-wider hover:text-gray-600 transition-colors">
                                Mundo Raymon
                            </button>
                        </div>
                    </nav>

                    {/* Utilities */}
                    <div className="hidden lg:flex items-center space-x-6">
                        <button className="flex items-center text-xs font-bold uppercase tracking-wider hover:text-gray-600">
                            <Globe className="w-4 h-4 mr-1" />
                            ES
                        </button>
                        <button className="text-gray-900 hover:text-gray-600">
                            <MapPin className="w-5 h-5" />
                        </button>
                        <button className="text-gray-900 hover:text-gray-600">
                            <Search className="w-5 h-5" />
                        </button>
                        <button className="bg-black text-white px-4 py-2 text-xs font-bold uppercase tracking-wider hover:bg-gray-800 transition-colors">
                            Tienda B2B
                        </button>
                    </div>

                    {/* Mobile menu button */}
                    <div className="lg:hidden flex items-center">
                        <button
                            onClick={() => setIsMenuOpen(!isMenuOpen)}
                            className="text-gray-900 hover:text-gray-600 p-2"
                        >
                            {isMenuOpen ? <X className="w-6 h-6" /> : <Menu className="w-6 h-6" />}
                        </button>
                    </div>
                </div>
            </div>

            {/* Mobile Menu */}
            {isMenuOpen && (
                <div className="lg:hidden bg-white absolute w-full border-t border-gray-100 h-screen overflow-y-auto">
                    <div className="px-4 pt-2 pb-3 space-y-1">
                        <Link to="#" className="block px-3 py-4 text-lg font-bold uppercase border-b border-gray-100">
                            E-Bikes
                        </Link>
                        <Link to="#" className="block px-3 py-4 text-lg font-bold uppercase border-b border-gray-100">
                            Bicicletas
                        </Link>
                        <Link to="/accesorios" className="block px-3 py-4 text-lg font-bold uppercase border-b border-gray-100">
                            Accesorios
                        </Link>
                        <Link to="#" className="block px-3 py-4 text-lg font-bold uppercase border-b border-gray-100">
                            Mundo Raymon
                        </Link>
                    </div>
                    <div className="px-4 py-4 space-y-4">
                        <button className="flex items-center text-sm font-bold uppercase">
                            <Globe className="w-4 h-4 mr-2" />
                            ES
                        </button>
                        <button className="flex items-center text-sm font-bold uppercase">
                            <MapPin className="w-4 h-4 mr-2" />
                            Concesionarios
                        </button>
                    </div>
                </div>
            )}
        </header>
    );
};

export default Header;
